@import "style/mixins.module.scss";

.radio-card {
    align-items: start;

    @include respond-to("tablet") {
        align-items: center;
    }
}

.content {
    display: block;
    margin-left: 8px;

    @include respond-to("tablet") {
        display: flex;
        justify-content: space-between;
        flex: 1;
    }
}

.date,
.time,
.address {
    margin: 0;
}

.time,
.date {
    font-weight: 700;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("tablet") {
        font-size: 22px;
        line-height: 32px;
    }
}

.date-content {
    display: flex;
    align-items: center;
    min-width: auto;
    margin-bottom: 8px;

    @include respond-to("tablet") {
        margin-bottom: 0;
        min-width: 270px;
    }
}

.pipe-icon {
    margin: 0 8px;
}

.pin-icon {
    display: none;

    @include respond-to("tablet") {
        display: block;
        margin-right: 11px;
    }
}

.address {
    display: block;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("tablet") {
        max-width: 220px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
    }

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
        max-width: 500px;
    }

    @include respond-to("laptop-L") {
        max-width: 800px;
    }
}

.address-content {
    display: flex;
    align-items: center;
    flex-basis: 70%;

    @include respond-to("tablet") {
        flex-basis: 60%;
    }
}
