@import "style/mixins.module.scss";

.root {
    position: relative;
}

.list {
    position: relative;

    display: flex;
    flex-wrap: wrap;

    overflow: hidden;
}

.container {
    display: flex;
    flex-direction: unset;
    margin: 0;
    padding-left: 0;
}

.item {
    box-shadow: 0px 0px 1px 8px white;
    list-style-type: none;
}

.title {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        margin-bottom: 40px;
    }
}

.arrow {
    position: absolute;
    z-index: 10;
    width: 56px;
    top: 50%;
    transform: translateY(-50%);
    height: 56px;
    background-color: #fff;
    border-radius: 2px;
    border: none;
    padding: 0;
    outline: none;
}

.container-arrow {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%);
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.arrow-left {
    left: 28px;
}

.arrow-right {
    right: 28px;
}

.arrow-icon {
    width: 24px;
    height: 24px;
}

.arrow-icon-right {
    transform: rotate(180deg);
}

.image {
    object-fit: cover;
    display: block;
    height: 240px;
    width: 100%;

    @include respond-to("tablet") {
        height: 520px;
    }
}
