@import "style/mixins.module.scss";

.container {
    width: 100%;
    padding: 0 24px;

    @include respond-to("laptop") {
        padding: 0 40px;
    }

    @include respond-to("laptop-L") {
        width: 1208px;
        margin: 0 auto;
        padding: 0;
    }
}

.first-visit {
    margin-bottom: 80px;

    @include respond-to("laptop") {
        margin-bottom: 120px;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 240px;
    }
}

.slider {
    margin-bottom: 80px;

    @include respond-to("laptop") {
        margin-bottom: 75px;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 160px;
    }
}
