@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;

    @include respond-to("laptop") {
        flex-direction: column;
    }
}

.title {
    margin-bottom: 24px;
}

.appointment-info {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    border-bottom: 1px solid rgba(66, 78, 88, 0.1);

    @include respond-to("laptop") {
        flex-direction: row;
    }
}

.appointment-item {
    @include color("gray", "-4");

    display: flex;
    flex-direction: column;
    flex-basis: 50%;

    padding-bottom: 16px;
    padding-right: 8px;
    font-size: 17px;
    line-height: 24px;

    &:last-child {
        padding-right: 0;
    }

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }

    &_price {
        flex-basis: auto;
    }

    &__title {
        margin-bottom: 8px;
    }
}

.calendar {
    position: relative;
    display: flex;
    width: 100%;
    flex: 1;
    padding-top: 16px;
    padding-bottom: 16px;
    border-bottom: 1px solid rgba(66, 78, 88, 0.1);
    margin-bottom: 40px;

    @include color("gray", "-4");

    &__icon {
        margin-right: 16px;
    }

    &__title {
        margin-bottom: 0;
        padding-right: 30px;
        font-size: 17px;
        line-height: 24px;
    }
}

.link {
    display: flex;
    position: relative;
    padding-bottom: 16px;
    border-bottom: 1px solid rgba(66, 78, 88, 0.1);
    cursor: pointer;

    @include color("gray", "-4");

    &__title {
        margin: 0;
        font-size: 17px;
        line-height: 24px;
    }
}

.address {
    @include respond-to("laptop") {
        display: grid;
        grid-template-columns: 144px auto;
    }

    &__title {
        font-size: 17px;
        line-height: 24px;

        @include respond-to("laptop") {
            font-size: 22px;
            line-height: 32px;
        }
    }

    &__column {
        &_label {
            margin-bottom: 8px;
            @include respond-to("laptop") {
                grid-column: 1;
                grid-row: 1;
            }
        }

        &_calendar {
            width: 100%;
        }

        &_map {
            width: 100%;
            margin-bottom: 16px;

            @include respond-to("laptop") {
                grid-column: 1;
                grid-row: 2 / 4;
            }
        }

        &_address {
            margin-bottom: 14px;
            @include respond-to("laptop") {
                grid-column: 2;
                grid-row: 2;
                padding-left: 24px;
            }
        }
        &_link {
            @include respond-to("laptop") {
                grid-column: 2;
                grid-row: 3;
                padding-left: 24px;
            }
        }
    }
}

.map {
    display: block;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: -1px 4px 10px 0px rgba(34, 60, 80, 0.2);
}

.link {
    display: flex;
    position: relative;
    padding: 16px 0;
    border-top: 1px solid rgba(66, 78, 88, 0.1);
    border-bottom: 1px solid rgba(66, 78, 88, 0.1);

    @include color("gray", "-4");

    &__title {
        margin: 0;
        font-size: 17px;
        line-height: 24px;
    }

    @include respond-to("laptop") {
        border-bottom: 1px solid rgba(66, 78, 88, 0.1);
    }
}

.pin-icon {
    margin-right: 12px;
}

.right-icon {
    position: absolute;
    right: 0;
}

.event-price {
    white-space: nowrap;
}
