@import "style/mixins.module";

.body {
    background-color: get_color("gray", "-5");

    @include respond-to("tablet") {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}

.logo {
    display: block;
    height: 28px;
    margin-bottom: 20px;
    width: 140px;

    @include respond-to("tablet") {
        position: absolute;
        top: 24px;
        left: 24px;
        margin-bottom: 0;
        width: 150px;
        height: 32px;
    }

    svg {
        width: 100%;
        height: 100%;
    }
}

.content {
    width: 100%;
    max-width: 600px;
    text-align: center;
    padding: 25px;
}

.label {
    margin-bottom: 25px;
}

.label-icon {
    margin: 70px 0 20px;

    @include respond-to("laptop") {
        margin: 0 0 25px 0;
    }
}

.button {
    margin-bottom: 40px;

    @include respond-to("laptop") {
        margin-bottom: 64px;
    }
}

.contacts-list {
    list-style-type: none;
    padding-left: 0;
    font-size: 17px;
    line-height: 24px;
    font-weight: 500;

    &__item {
        width: 100%;
        border-bottom: 0.5px solid rgba(66, 78, 88, 0.1);

        @include respond-to("laptop") {
            border-bottom: none;
            width: auto;
        }
    }

    &__text {
        margin-bottom: 0;
    }

    &__icon {
        @include respond-to("laptop") {
            margin-right: 18px;
        }
    }

    &__link {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        padding-top: 16px;
        padding-bottom: 16px;

        @include respond-to("laptop") {
            padding-top: 0;
            padding-bottom: 0;
            flex-direction: row-reverse;
        }
    }

    @include respond-to("laptop") {
        max-width: 480px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        width: 100%;
        padding-left: 24px;
        padding-right: 24px;
    }
}
