@import "style/mixins.module.scss";
@import "./Animate.module.scss";

.root {
    position: relative;
    display: flex;
    flex-direction: column;

    background-color: var(--color-light-gray);
    padding-top: 24px;
    margin-bottom: 152px;

    @include respond-to("laptop") {
        padding-bottom: 40px;
        margin-bottom: 240px;
        padding-top: 0;
        padding-bottom: 40px;
    }

    &_has-list {
        padding-top: 25px;
        padding-bottom: 0;
        height: auto;
    }
}

.main-container {
    @include respond-to("laptop") {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }

    &_has-list {
        padding-bottom: 40px;
    }
}

.desktop-booking-list {
    display: none;

    @include respond-to("laptop") {
        background-color: get_color("gray", "0");
        display: block;
        position: relative;

        &::after,
        &::before {
            content: "";
            position: absolute;
            top: 0;
            width: 40px;
            height: 40px;
            background-color: var(--color-light-gray);
        }

        &::before {
            left: 0;
        }

        &::after {
            right: 0;
        }
    }
}

.mobile-booking-list {
    display: block;

    @include respond-to("laptop") {
        display: none;
    }
}

.appointment-details {
    margin-bottom: 40px;
}

.radio-card {
    margin-bottom: 40px;
}

.column {
    @include respond-to("laptop") {
        width: calc(50% - 32px);
    }

    &_text {
        @include respond-to("laptop") {
            padding-top: 75px;
        }
    }

    &_video {
        margin-bottom: -35px;

        @include respond-to("laptop") {
            margin-bottom: auto;
        }
    }

    &_has-list {
        @include respond-to("laptop") {
            position: relative;
            margin-top: 54px;
            top: 0;
        }
    }
}

.header {
    margin-top: 25px;
    margin-bottom: 40px;

    @include respond-to("laptop") {
        margin-bottom: 8px;
    }
}

.logo {
    display: block;
    width: 140px;
    height: 28px;

    @include respond-to("laptop") {
        width: 160px;
        height: 32px;
    }

    & svg {
        width: 100%;
        height: 100%;
    }
}

.title {
    margin-bottom: 40px;
}

.subtitle {
    margin-bottom: 8px;
}

.list-title {
    margin-bottom: 16px;
}

.description {
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }
}

.bookings {
    @include respond-to("laptop") {
        padding: 40px;
        margin: 0 40px;
        background-color: get_color("gray", "0");
        box-shadow: 0 6px 14px -6px rgba(24, 39, 75, 0.12),
            0 10px 32px -4px rgba(24, 39, 75, 0.1);
    }
}

.bookings-title {
    display: none;

    @include respond-to("laptop") {
        display: block;
    }
}

.actions {
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;

    &_has-list {
        @include respond-to("laptop") {
            margin-bottom: 0;
        }
    }

    @include respond-to("laptop") {
        flex-direction: row;
    }

    &__button {
        margin: 8px 0;

        @include respond-to("laptop") {
            margin: 0 8px;

            &:first-child {
                margin-left: 0;
            }
        }
    }
}

.video {
    display: none;

    @include respond-to("laptop") {
        display: block;
    }
}

.video[poster] {
    min-width: 100%;
}

.image {
    display: block;
    width: 100%;
    height: auto;
    object-fit: cover;

    @include respond-to("laptop") {
        display: none;
    }
}

.ticker-wrap {
    position: absolute;
    bottom: -100px;

    width: 100%;

    font-family: inherit;
    font-weight: 700;
    font-size: 13px;
    line-height: 20px;
    text-transform: uppercase;

    list-style: none;
    @include color("primary", "red");
    overflow: hidden;

    @include respond-to("laptop") {
        display: none;
    }
}

.first-visit-text {
    display: none;

    @include respond-to("laptop") {
        position: absolute;
        top: -40px;
        right: 0;

        display: block;

        animation-iteration-count: infinite;
        animation-timing-function: linear;
        animation-name: rotating;
        animation-duration: $duration;
    }

    @include respond-to("laptop") {
        right: -40px;
    }
}

.ticker {
    display: flex;
    align-items: center;
    padding-right: 100%;

    list-style: none;
    white-space: nowrap;

    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-name: ticker;
    animation-duration: $duration;
}

.ticker-item {
    position: relative;
    margin-right: 40px;

    &::after {
        content: "";
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: -20px;

        width: 4px;
        height: 4px;
        display: block;

        border-radius: 50%;
        background-color: get_color("primary", "red");
    }
}
