@import "style/mixins.module";

.modal-content {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    background-color: get_color("gray", "0");
    height: 100%;
    justify-content: center;
}

.modal-label {
    margin: 24px 0;

    @include respond-to("laptop") {
        text-align: center;
    }
}

.modal-actions {
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-content: center;
    align-items: center;

    button {
        margin: 8px 0;
        min-width: 200px;
    }

    @include respond-to("laptop") {
        align-items: center;
    }

    @include respond-to("container-default") {
        flex-direction: row;

        button {
            margin: 8px;
        }
    }
}
