@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;

    @include color("gray", "-4");

    @include respond-to("laptop") {
        flex-direction: row;

        margin-bottom: 80px;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 240px;
    }
}

.title {
    margin-bottom: 16px;
}

.description {
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop-L") {
        font-size: 22px;
        line-height: 32px;
    }
}

.medics-info {
    width: auto;

    @include respond-to("laptop") {
        width: 50%;
        margin-right: 80px;
        margin-top: 35px;
    }

    @include respond-to("laptop-L") {
        width: 40%;
        margin-top: 80px;
    }
}

.medics-images {
    display: flex;
    width: auto;
    margin-bottom: 80px;

    @include respond-to("laptop") {
        width: 50%;
        margin-bottom: 30px;
    }

    @include respond-to("laptop-L") {
        width: 60%;
        margin-bottom: 0;
    }
}

.medic-image-wrapper {
    width: 100%;
    position: relative;

    &:first-child {
        margin-right: 7px;
    }

    &:after {
        content: "";
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 80%;
        width: 100%;
        background: #eef3f4;
        z-index: -1;
    }
}

.medic-image {
    width: 100%;
    height: 100%;

    @include respond-to("laptop") {
        background-repeat: no-repeat;
        background-size: cover;
    }
}
